









































import { Vue, Component } from "vue-property-decorator";

@Component
export default class HealthRecord extends Vue {
  private search: string = "";
  private loading: Boolean = true;
  private overlay: Boolean = false;
  private headers: Object = [
    { text: "病歷號碼", value: "id" },
    { text: "身分證", value: "identifier" },
    { text: "姓名", value: "name" },
    { text: "看診日期", value: "date" },
    { text: "操作", value: "action", sortable: false }
  ];
  private desserts: Array<Object> = [];
  created() {
    this.axios
      .get("/healthrecord/all")
      .then(data => data.data)
      .then(({ healthrecords }) => {
        this.desserts = healthrecords["entry"];
        this.loading = false;
      })
      .catch(data => {});
  }
  deleteItem(item: any) {
    if (confirm("確定要刪除這個病歷嗎?")) {
      this.overlay = true;
      const index = this.desserts.indexOf(item);
      var order = "/healthrecord/" + item["id"];
      this.axios
        .delete(order)
        .then(data => data.data)
        .then(({ ok }) => {
          this.desserts.splice(index, 1);
          this.overlay = false;
          this.$toasted.show(`刪除成功`, {
            type: "success",
            position: "top-right",
            duration: 3000
          });
        })
        .catch(data => {
          this.overlay = false;
          this.$toasted.show(`刪除失敗，請重新刪除一次`, {
            type: "error",
            position: "top-right",
            duration: 3000
          });
        });
    }
  }
  showItem(item: any) {
    this.$router.push({
      path: "/admin/healthrecord/healthrecordform",
      query: { action: "show", id: item["id"] }
    });
  }
}
